<template>
  <el-form :model="item3" ref="typeForm" :rules="typeFormRules" :inline="true">
    <el-form-item prop="type">
      <el-select
        v-model="item3.type"
        style="max-width: 100px; min-width: 100px;"
        name="oxve"
        class="input-inline"
        @change="handleChange"
      >
        <!-- <el-option value="cishu" label="次数"></el-option>
        <el-option value="tianshu" label="天数"></el-option> -->
        <template v-for="attrChildItem in attrList">
          <el-option
            :value="attrChildItem.value"
            :label="attrChildItem.name"
            :key="attrChildItem.value"
            :disabled="attrChildItem.value === 'day' && isDisableDaySelect"
          >
          </el-option>
        </template>
      </el-select>
    </el-form-item>
    <!-- {{attrChildItem}}
    {{isDisableDaySelect}} -->
    <el-form-item>
      <el-select
        v-model="item3.operator"
        style="max-width: 100px; min-width: 100px;"
        name="oxve"
        class="input-inline"
      >
        <el-option value="="></el-option>
        <el-option value=">="></el-option>
        <el-option value="<="></el-option>
        <el-option value=">"></el-option>
        <el-option value="<"></el-option>
      </el-select>
    </el-form-item>

    <el-form-item prop="value">
      <el-input
        placeholder="请输入"
        v-model="item3.value"
        clearable
        style="max-width: 100px; min-width: 100px;"
      >
      </el-input>
    </el-form-item>
  </el-form>
</template>

<script>
export default {
  data () {
    return {
      attrList: [],
      isDisableDaySelect: false,
      typeFormRules: {}
    }
  },
  // props: ['tags', 'crowd', 'specialTags', 'i'],
  props: {
    item3: {
      type: Object,
      default: () => {}
    },
    options: {
      type: Array,
      default: () => []
    },
    childItem: {
      type: Object,
      default: () => {}
    },
    isRequired: {
      type: Boolean,
      default: true
    }
  },
  watch: {
    isRequired: { // 是否必填
      handler (val) {
        this.typeFormRules = {
          value: [
            { required: val, message: '请输入值', trigger: 'blur' },
            { required: val, message: '请输入值', trigger: 'change' }
          ],
          type: [
            { required: true, message: '请选择', trigger: 'change' }
          ]
        }
      },
      immediate: true
    },
    childItem: {
      handler (val) {
        const weekRang = val.bav.weekRang.value
        const timeRange = val.bav.timeRange.value
        // 当选择了星期范围或者时间区间时，禁用【天数】选项
        if (weekRang.length === 0 && timeRange.length === 0) {
          this.isDisableDaySelect = false
        } else {
          this.isDisableDaySelect = true
        }
      },
      deep: true,
      immediate: true
    },
    options: {
      handler (val) {
        this.attrList = val.map(item => {
          return {
            name: item.dictLabel,
            value: item.dictValue,
            field: item.tableField
          }
        })
      },
      immediate: true
    }
  },
  methods: {
    handleChange (val) {
      const obj = this.attrList.find(item => item.value === val)
      console.log('obj==>', obj)
      this.item3.field = obj.field
    }
  }
}
</script>
<style scoped  lang="stylus">
.label-item span, .oc-item {
  margin-right: 10px;
}
.el-date-editor.el-input, .el-date-editor.el-input__inner {
  width: 100%;
}
</style>
